import React, { Component } from 'react'
import { Grid, Segment, Icon, Menu, Image, Header } from 'semantic-ui-react'
import { Link } from "react-router-dom";
import VKM_Logo from '../img/VKM_logo_v5.png';

export default class HeaderSui extends Component {
  state = { activeItem: '', menuVisible: false }
  
  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name, menuVisible: !this.state.menuVisible })
  }

  render() {
    const { activeItem } = this.state

    const MenuList = [
      {
        nameActive: 'Info&Skills',
        nameIcon: 'wrench',
        to: '/'
      }, 
      {
        nameActive: 'Projects',
        nameIcon: 'folder open',
        to: '/projects'
      }, 
      {
        nameActive: 'Contact',
        nameIcon: 'call',
        to: '/contact'
      }, 
      {
        nameActive: 'GitHub',
        nameIcon: 'github',
        to: ''
      },
    ]
    
  const MenuItems = MenuList.map((item) => {
    return (
      <Link 
       to={item.nameActive === 'GitHub' ? {pathname: 'https://github.com/VKMStudioX'}  : item.to }
       target={item.nameActive === 'GitHub' ? "_blank" : "_self"}
       key={item.nameActive}
      >
        <Menu.Item inverted 
          key={item.nameActive}
          name={item.nameActive}
          active={activeItem === item.nameActive}
          onClick={this.handleItemClick}
        >
          <Icon name={item.nameIcon} />
          {item.nameActive}
        </Menu.Item>
      </Link>
    );
  });

  const menuColor='grey'


const MenuHeader = (
  <Menu icon='labeled' inverted className='header-menu' borderless floated='right' size='tiny'> 
{MenuItems}
      </Menu>
  )

  const MenuHeaderMobile = (
    <Menu icon='labeled' inverted className='header-menu-mobile' borderless floated='right' size='mini'>
         <Menu.Item inverted className='header-menu' name='Menu' open={this.state.menuVisible} onClick={() => this.setState({ menuVisible: !this.state.menuVisible })}>
          <Icon name='list ul' /></Menu.Item>
        </Menu>
    )


    return (
      <>
      <Segment>
<Grid columns='equal'>
<Grid.Row className='sticky'>
  <Grid.Column> 
    <Link to='/'><Image src={VKM_Logo} alt="logo" className="header-logo" /></Link>
  </Grid.Column> 
   <Grid.Column only='wideScreen largeScreen computer'>
    {/* <Segment color={menuColor} inverted> */}
     <Header className="header-km">
    <span className="header-km">Krzysztof Meyer</span><span className="header-km">Automation and robotics engineer &amp; Web developer</span>
  </Header>  
    {/* </Segment> */}
   </Grid.Column> 
   <Grid.Column only='wideScreen largeScreen computer tablet'>
  {/* <Segment color={menuColor} inverted textAlign='right'>  */}
      {MenuHeader}
    {/* </Segment> */}
  </Grid.Column> 
 <Grid.Column only='mobile '> 
    
{MenuHeaderMobile}
{this.state.menuVisible ? MenuHeader : null}
  
  </Grid.Column> 
</Grid.Row>
</Grid>
</Segment>
</>
    )
  }
}